<template>
  <div class="homeen">    
    <div class="container-fluid">
      <br>
      <div class="row">
        <div class="col-md-4">
          <!-- sub-title="системный программист/администратор" -->
          <b-card header="<h6>Personal page</h6>"
            class="mb-2 bg2"
            title="Timur Galutvo"
            show-footer
            align="left"
            >
            <img class=".roundborder" src="../assets/avatar.jpg" width="250" height="250">
            <br>
            <small>System programmer/IT and DBA administrator</small>
            <br><br>
            <p>I am {{ myage }} years old.<br>Live in Krasnodar, Russia.</p>
            <small slot="footer" class="text-muted">
              <a href="mailto:to-timur@yandex.ru"><icon name="envelope" scale="2" ></icon></a>
              <a href="https://github.com/GTimur" alt="github link"> <icon name="github" scale="2"></icon></a>
            </small>
          </b-card>
        </div>
        <div class="col-md-8">
          <b-card header="<h6>A little bit of information</h6>"
            class="mb-2 bg2 display: none;"
            show-footer
            align="left">             
          <h6>The last 10 years my work was connected with the following objectives: integrated development of the it infrastructure of several companies, administration, applications and databases (last 7 years - mainly banking and Oracle DBMS), business continuity of enterprise information systems (storage systems and data backup, it infrastructure) and application development (front-end for working with data in a DBMS, utilities, and converters) in various programming languages (C/C++, JavaEE, C#, Python, Golang).</h6>
          <br>
          <h6>I am actively interested in software development. The main number my code is written in Golang, C#, Java and PL / SQL.</h6>
          <br>
          <h6>At the current time I pay more attention to the practice of using language Golang.</h6>
            <small slot="footer" class="text-muted">
            </small>
          </b-card>
        </div>
        <div class="col-md-0">
        </div>
      </div>        
    </div>
  </div>
</template>

<script>
var moment = require('moment')

export default {
  name: 'homeen',
  data () {
    return {
      myage: moment().diff('1983-01-27', 'years')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}

img {
  max-width: 100%;
  height: auto;
  width: auto\9; /* ie8 */
}

img, .roundborder {
-moz-border-radius:1px;
-khtml-border-radius:1px;
-webkit-border-radius:1px;
border-radius:10px;
/*border:1px solid white;*/
}


.bg2 {
  background-color: #FFFFF0;
}


</style>



