<template>
  <div class="experience bg2">
    <div class="row">
      <div class="col-md-1">
      </div>
      <div class="col-md-10">
        <br>
        <h2 align="center">Опыт работы</h2>
      </div>
    </div>  
    <div class="container-fluid fixHeight">
      <div class="row">
        <div class="col-md-1">
        </div>
        <div class="col-md-10">
          <b-tabs>
            <b-tab title="Общее" active>
              <expznania :curlang="this.getcurlang()"></expznania>
            </b-tab>
            <b-tab title="Резюме">
              <expresume></expresume>
                  <div class="fixHeight10">
      <hr>
    </div>
            </b-tab>
          </b-tabs>
        </div>
        <div class="col-md-1">
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import ExpZnania from '@/components/ExpZnania'
import ExpResume from '@/components/ExpResume'

export default {
  name: 'experience',
  components: {
    expznania: ExpZnania,
    expresume: ExpResume
  },
  methods: {
    getcurlang: function () {
      return this.$parent.curlang
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}

.fixHeight {
  min-height: 100%;
/*  min-height: 10vh;
  max-height: 60vh;*/
  overflow: hidden;
}

.fixHeight10 {  
  height: 15vh;
  min-height: 15vh;
  /*margin: auto;*/
}

.bg2 {
  background-color: #FFFFF0;
}

</style>



