<template>
  <div class="home">
    <span v-show="this.$parent.curlang === 1">
    <div class="container-fluid">
      <br>
      <div class="row">
        <div class="col-md-4">
          <!-- sub-title="системный программист/администратор" -->
          <b-card header="<h6>Персональная страница</h6>"
            class="mb-2 bg2"
            title="Тимур Галутво"
            show-footer
            align="left"
            >
            <img class=".roundborder" src="../assets/avatar.jpg" width="250" height="250">
            <br>
            <small>Системный программист/администратор</small>
            <br><br>
            <p>Мне {{ myage }}.<br>Живу в городе Краснодар.</p>
            <small slot="footer" class="text-muted">
              <a href="mailto:to-timur@yandex.ru"><icon name="envelope" scale="2" ></icon></a>
              <a href="https://github.com/GTimur" alt="github link"> <icon name="github" scale="2"></icon></a>
            </small>
          </b-card>
        </div>
        <div class="col-md-8">
          <b-card header="<h6>Немного информации</h6>"
            class="mb-2 bg2 display: none;"
            show-footer
            align="left">             
          <h6>Последние 10 лет моя работа связана с комплексным развитием ИТ инфраструктуры нескольких предприятий, администрированием приложений и баз данных (последние 7 лет - это преимущественно банковское ПО и СУБД Oracle), с обеспечением непрерывности работы информационных систем предприятия (системы хранения и резервирования данных, ИТ-инфраструктура), а так же с разработкой прикладных программ (фронтэнды для работы с данными в СУБД, утилиты и конвертеры) на различных языках программирования (C/C++, JavaEE, C#, Python, Golang).</h6>
          <br>
          <h6>Я активно интересуюсь разработкой программного обеспечения. Основное количество моего кода написано на Golang, C#, Java и PL/SQL.</h6>
          <br>
          <h6>В текущее время уделяю больше внимания практике использования языка Golang.</h6>
            <small slot="footer" class="text-muted">
            </small>
          </b-card>
        </div>
        <div class="col-md-0">
        </div>
      </div>        
    </div>
    </span>
    <span v-show="this.$parent.curlang === 2">
      <homeen></homeen>
    </span>
  </div>
</template>

<script>
var moment = require('moment')
import HomeEN from '@/components/HomeEN'

export default {
  name: 'home',
  data () {
    return {
      myage: moment().diff('1983-01-27', 'years')
    }
  },
  components: {
    homeen: HomeEN
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}

img {
  max-width: 100%;
  height: auto;
  width: auto\9; /* ie8 */
}

.bg2 {
  background-color: #FFFFF0;
}

img, .roundborder {
-moz-border-radius:1px;
-khtml-border-radius:1px;
-webkit-border-radius:1px;
border-radius:10px;
/*border:1px solid white;*/
}

.notvisible {
  display: none;
}

</style>



