<template>
  <div class="expznania">
    <br>    
    <h4><span v-if="curlang === 1">Мои навыки:</span>
    <span v-else>My work experience:</span></h4>
    <br>
    <!--h5>Языки: <span v-for="lang in langs"><b-badge variant="primary" pill>{{ lang }}</b-badge>&nbsp;</span></h5-->
    <p>
    <strong><span v-if="curlang === 1">Языки: </span><span v-else>Languages: </span></strong> <span v-for="(lang, index) in langs"><a class="article-category-link" href="javascript:void(0)">{{ lang }}</a><span v-if="index != langs.length-1">&nbsp;&middot;&nbsp;</span></span>    
    </p>

    <p>
    <strong><span v-if="curlang === 1">Опыт работы: </span><span v-else>Skills: </span></strong> <span v-for="(skill, index) in skills"><a class="article-category-link" href="javascript:void(0)">{{ skill }}</a><span v-if="index != skills.length-1">&nbsp;&middot;&nbsp;</span></span>
    </p>

    <p>
    <strong><span v-if="curlang === 1">Технологии: </span><span v-else>Products/platforms: </span></strong><span v-for="(tech, index) in techs"><a class="article-category-link" href="javascript:void(0)">{{ tech }}</a><span v-if="index != techs.length-1">&nbsp;&middot;&nbsp;</span></span>
    </p>

    <p>
    <strong><span v-if="curlang === 1">Вендоры: </span><span v-else>Vendors: </span></strong> <span v-for="(vendor, index) in vendors"><a class="article-category-link" href="javascript:void(0)">{{ vendor }}</a><span v-if="index != vendors.length-1">&nbsp;&middot;&nbsp;</span></span>
    </p>    

    <p>
    <strong><span v-if="curlang === 1">Банковский опыт: </span><span v-else>Banking: </span></strong> <span v-for="(b, index) in banking"><a class="article-category-link" href="javascript:void(0)">{{ b }}</a><span v-if="index != banking.length-1">&nbsp;&middot;&nbsp;</span></span>
    </p>    

    <hr>
    <br>
  </div>
</template>

<script>
export default {
  name: 'expznania',
  props: ['curlang'],
  data () {
    return {
      langs: ['C', 'C++', 'Golang', 'Assembler', 'C#', 'Java', 'Python', 'JavaScript', 'PL/SQL', 'HTML/CSS', 'Bash'],
      skills: ['DBA', 'System Administrator', 'Internet Banking', 'Banking Automation', 'Disaster Recovery', 'Business Continuity', 'Backup', 'Datacenter'],
      techs: ['Linux', 'Unix', 'Windows Server', 'Oracle Database', 'MSSQL', 'Hyper-V', 'Vmware ESXi', 'LTO', 'LTFS'],
      vendors: ['IBM', 'DELL', 'CISCO', 'APC'],
      banking: ['ABS BANK 21 CENTURY (INVERSION)', 'iBank2 (Internet-Banking platform)', 'PTK PSD', 'TBSVK', '1C8']
    }
  }
}
</script>



