<template>
  <div id="app"> 
    <div id="main" class="container-fluid fluid-mod">
      <mainmenu></mainmenu>
      <br>
      <div class="row lpad ">
        <div class="col-md-1">
        </div>
        <div class="col-md-10">
          <div class="container-fluid containerMod makeNoScroll roundborder bg">
            <router-view></router-view>
          </div>
        </div>
      </div>          
    </div>
    <div id="footer">
      <div class="container" align="center">        
        <small>{{ version }}</small>
      </div>
    </div>
  </div>
</template>

<script>
import Vue from 'vue'
import BootstrapVue from 'bootstrap-vue'
Vue.use(BootstrapVue)
import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'
import MainMenu from '@/components/MainMenu'

export default {
  name: 'app',
  data () {
    return {
      version: '(C) 2017 by GTG (v.1.2)',
      langs: {1: 'ENG', 2: 'РУС'},
      langphrs: {1: 'Lang:', 2: 'Язык:'},
      curlang: 1
    }
  },
  methods: {
    changelang: function () {
      if (this.curlang === 1) {
        this.curlang = 2
      } else {
        this.curlang = 1
      }
    },
    getcurlang: function () {
      return this.langs[this.curlang]
    },
    getcurlangphr: function () {
      return this.langphrs[this.curlang]
    }
  },
  components: {
    mainmenu: MainMenu
  },
  computed: {
    vueRoot () {
      return this.$root
    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /*text-align: center;*/
  color: #2c3e50;
  margin-top: 50px;
}

/*.myJumbotron {
  background-color: #89AC76;  
  color: white;
  padding: 10px;
}*/

body {
  padding-bottom: 1px;
  padding-top: 1px;
}

.special-card {
  background-color: rgba(245, 245, 245, 1);
  opacity: .9;  
}

.makeScroll {
  height: 100%;
  min-height: 10vh;
  max-height: 80vh;
  /*overflow-y: scroll;*/
  overflow-y: auto;
  margin: auto;
}

.makeNoScroll {
  height: 100%;
  min-height: 10vh;
  max-height: 80vh;
  overflow: hidden;
}

.containerMod {
background-color: rgba(245, 245, 245, 1);
opacity: .9; 
margin: 0px auto;
}

.white {    
    color: #FFFFFF;    
}

.roundborder {
-moz-border-radius:4px;
-khtml-border-radius:4px;
-webkit-border-radius:4px;
border-radius:4px;
/*border:1px solid white;*/
}

/* Sticky footer styles
-------------------------------------------------- */
html {
  position: relative;
  min-height: 100%;
}
body {
  /* Margin bottom by footer height */
  margin-bottom: 26px;
}
#footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  /* Set the fixed height of the footer here */
  height: 26px;
  background-color: #f5f5f5;
}
.container {
  width: auto;  
  padding: 0 15px;
}

.fluid-mod {
  width: auto;
  max-width: 100%;
  height: 100%;  
}

.lpad {
  padding-left:10em;
  padding-right:10em;
}

.bg {
  background-color: #FFFFE0;
}

.bg2 {
  background-color: #66CCCC;
}

/* xs - устройства (до 576px) */
/* CSS для ширины, которая меньше 575px (включительно) */
 
/* sm-устройства (больше или равно 576px) */
@media (min-width: 576px) {
  /* CSS для: 576px <= ширины <= 767px */
  .lpad {
  padding-left:0em;
  padding-right:0em;

}
}
 
/* md-устройства (больше или равно 768px) */
@media (min-width: 768px) {
  /* CSS для: 768px <= ширины <= 991px */
    .lpad {
    padding-left:2em;
  padding-right:2em;

}
}
 
/* lg-устройства (больше или равно 992px) */
@media (min-width: 992px) {
  /* CSS для: 992px <= ширины <= 1119px */
    .lpad {
    padding-left:5em;
  padding-right:5em;

}
}
 
/* xl-устройства (больше или равно 1200px) */
@media (min-width: 1200px) {
  /* CSS для: ширины >= 1200px */
    .lpad {
    padding-left:10em;
    padding-right:10em;

}
}

</style>



